.profile {
  margin: 0;
  padding: 0;
  width: 300px;

  display: flex;
  align-items: center;
  flex-direction: column;

  border: 1px solid #000000;
  border-radius: 10px;

  overflow: hidden;
}

.description {
  padding: 10px;

  background-color: #ffffff;
}

.avatar {
  margin: 0 0 10px 0;
  width: 100%;
}

.name,
.tag,
.location {
  margin: 0 0 5px 0;

  font-size: 16px;
  font-weight: normal;
}

.name {
  text-align: center;
  font-size: 24px;
  font-weight: bold;
}

.stats {
  display: flex;
  align-items: flex-start;
  justify-content: space-around;

  margin: -10px;
  padding: 0;
  width: 100%;

  list-style: none;
}

.stat {
  margin: 10px;
  width: calc((100%-10 * 6 / 3));

  display: flex;
  flex-direction: column;
  align-items: center;

  background-color: #ccdddd;
}

.label {
  margin: 0;
  font-weight: bold;
}
.quantity {
  margin: 0;
  font-weight: normal;
}
