.statistics {
  display: flex;
  flex-direction: column;
  align-items: center;

  margin: 0;
  padding: 10px;
  width: 300px;

  outline: 1px solid tomato;
}

.title {
  font-weight: bold;
  font-size: 18px;
  margin: 0 0 10px;
}

.stat-list {
  display: flex;
  flex-direction: row;
  justify-content: space-around;

  width: 100%;
  margin: -5px -5px 0px;
  padding: 0;

  list-style: none;
  background-color: #dddddd;
}

.item {
  margin: 5px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: calc((100%-5 * 5) / 5);
  background-color: #ffffff;
}

.label,
.percentage {
  font-size: 12px;
}

.label {
  font-weight: bold;
}
